body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input{
    width:300px;
    height:20px;
    color:orange;
    background-color:black;
    font-size:14px;
    padding-left:10px;
}

.input{
    height:30px;
    width:300px;
    font-size:23px;
    border-radius:23px;
}

ul{
    width:200px;
    text-align: center;
    font-size:22px;
    list-style-position: inside;
    list-style:none;
}

li{
    margin-left:10px;
    margin-right:10px;
}

.delete{
    width:30px;
    height:30px;
    background-image:url(data:image/png;base64,);
    border-radius:20px;
}

.delete:hover{
    background-image:url(data:image/png;base64,);
}

.mainDiv{
    background-color:green;
}
.crossDiv{
    float:left;
    width:5px;
}


.listDiv{
    float:left;
}
